.hero {
  background: linear-gradient(
    180deg,
    rgba(237, 247, 255, 0.1) 72.4%,
    #fff 100%
  );
  position: relative;
  padding-top: 120px;
  @include screen(laptop) {
    padding-bottom: 300px;
  }
  @include screen(desktop) {
    padding-top: 160px;
  }
  &__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      object-position: top;
    }
  }
  &__wrapper {
    position: relative;
    z-index: 2;
    align-items: flex-end;
    column-gap: 25px;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: 570px 1fr;
    }
  }
  &__image {
    position: relative;
    margin-top: 20px;
    @include screen(tablet) {
      margin-top: 0;
    }
    img {
      margin-left: 20px;
      @include screen(tablet) {
        margin-bottom: -60px;
      }
      @include screen(laptop) {
        margin-bottom: 0;
        position: absolute;
        bottom: -100px;
      }
      @include screen(desktop) {
        bottom: -140px;
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
    @include screen(tablet) {
      gap: 40px;
    }
  }
  &__title {
    font-size: 32px;
    font-weight: 900;
    line-height: 100%;
    letter-spacing: -0.64px;
    @include screen(laptop) {
      font-size: 64px;
    }
    span {
      color: $blue-400;
    }
  }
  &__items {
    min-height: 256px;
    display: flex;
    border-radius: 24px;
    padding: 24px;
    align-items: flex-start;
    border: 1px solid rgba(55, 70, 100, 0);
    flex-direction: column;
    width: 100%;
    gap: 16px;
    background: $white;
    box-shadow: 0px 0px 8px 0px rgba(2, 18, 87, 0.12);
    @include screen(tablet) {
      gap: 0;
      flex-direction: row;
      border-radius: 24px 0px 24px 24px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    &__footer {
      margin-top: 24px;
    }
    &:first-child {
      @include screen(tablet) {
        padding-right: 24px;
        border-right: 1px dashed rgba(55, 70, 100, 0.3);
      }
    }
    &:last-child {
      @include screen(tablet) {
        padding-left: 24px;
      }
    }
    h2 {
      text-transform: uppercase;
      margin-bottom: 8px;
      color: $blue-400;
      font-size: 14px;
      font-weight: 700;
      line-height: 24px;
      letter-spacing: 0.56px;
    }
    p {
      font-size: 14px;
      line-height: 24px;
      br {
        display: none;
        @include screen(tablet) {
          display: block;
        }
      }
    }
  }
  &__button {
    background-color: $blue-400;
    padding: 12px 16px;
    border: 1px solid $blue-400;
    border-radius: 6px;
    display: flex;
    align-items: center;
    gap: 12px;
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-500;
        span {
          color: $white;
        }
      }
    }
    span {
      text-transform: uppercase;
      color: $white;
      font-size: 11px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: 0.88px;
      @include screen(laptop) {
        transition: color $transition-default;
      }
    }
    &-transparent {
      background-color: transparent;
      border-color: $blue-400;
      span {
        color: $blue-400;
      }
    }
  }
  &__tip {
    font-size: 11px;
    line-height: 16px;
    opacity: 0.5;
    margin-top: 8px;
  }
  &__blocks {
    grid-column: span 2;
    width: 100%;
    margin-top: 40px;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(tablet) {
      margin-top: 72px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      gap: 0;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__link {
    color: $gray-400;
    font-size: 14px;
    line-height: 20px;
    border-bottom: 1px solid $gray-400;
    br {
      display: none;
      @include screen(tablet) {
        display: block;
      }
    }
    @include screen(laptop) {
      transition: color $transition-default, border-color $transition-default;
      &:hover {
        color: $blue-400;
        border-color: $blue-400;
      }
    }
  }
  &__block {
    position: relative;
    @include screen(laptop) {
      border-right: 1px dashed #c0c5d0;
      padding: 0 22px;
    }
    &:before {
      content: "";
      width: 5px;
      height: 5px;
      right: 0;
      transform: translateX(50%);
      position: absolute;
      top: 0;
      background-color: #c0c5d0;
      border-radius: 50%;
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    &:after {
      content: "";
      right: 0;
      transform: translateX(50%);
      position: absolute;
      bottom: 0;
      width: 5px;
      background-color: #c0c5d0;
      height: 5px;
      border-radius: 50%;
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    &:first-child {
      padding-left: 0;
      @include screen(laptop) {
        padding-right: 22px;
      }
    }
    &:last-child {
      padding-right: 0;
      border-right: none;
      @include screen(laptop) {
        padding-left: 22px;
      }
      h3 {
        @include screen(laptop) {
          margin-top: 48px;
        }
      }
      &:before,
      &:after {
        display: none;
      }
    }
    h3 {
      font-size: 18px;
      margin-top: 24px;
      font-weight: 700;
      line-height: 24px;
      margin-bottom: 16px;
      br {
        display: none;
        @include screen(tablet) {
          display: block;
        }
      }
    }
    &__header {
      display: flex;
      align-items: center;
      span {
        color: $gray-400;
        font-size: 12px;
        font-weight: 500;
        line-height: 16px;
        border-bottom: 1px solid $gray-400;
      }
      svg {
        margin-left: 4px;
      }
    }
  }
  &__icon {
    margin-right: 8px;
    border-bottom: none;
    img {
      width: 40px;
      height: 40px;
      min-width: 40px;
      min-height: 40px;
    }
  }
  &__demos {
    display: flex;
    align-items: flex-start;
    gap: 8px;
    flex-direction: column;
    @include screen(mobile) {
      gap: 16px;
      flex-direction: row;
      align-items: center;
    }
  }
  &__demo {
    width: 100%;
    padding: 8px;
    display: inline-block;
    border-radius: 16px;
    background-color: $white;
    @include screen(laptop) {
      &:hover {
        .hero__demo__inner {
          background-color: $green-500;
        }
      }
      width: auto;
      bottom: 90px;
      left: 55px;
      position: absolute;
      bottom: -20px;
    }
    @include screen(desktop) {
      left: 100px;
    }
    &:last-child {
      left: 280px;
      @include screen(desktop) {
        left: 380px;
      }
    }
    &__inner {
      transition: background-color $transition-default;
      justify-content: center;
      background-color: $green-400;
      border-radius: 8px;
      padding: 8px 16px;
      display: flex;
      align-items: center;
      gap: 29px;
      span {
        color: $white;
        font-weight: 500;
        font-size: 12px;
      }
    }
  }
}