.how {
  padding: 28px 0 45px;
  position: relative;
  z-index: 2;
  @include screen(laptop) {
    margin-top: -270px;
  }
  &__containers {
    margin-top: 64px;
  }
  &__buttons {
    display: flex;
    width: 100%;
  }
  &__button {
    width: 50%;
    padding: 6px 10px;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: $gray-700;
    font-weight: 600;
    border-bottom: 1px solid $gray-600;
    position: relative;
    transition: color $transition-default;
    @include screen(tablet) {
      font-size: 18px;
      padding: 18px;
      line-height: 24px;
    }
    &:before {
      width: 100%;
      height: 2px;
      content: "";
      left: 0;
      bottom: -1px;
      position: absolute;
      background-color: transparent;
      transition: background-color $transition-default;
    }
    &:after {
      position: absolute;
      content: attr(data-title);
      font-weight: 600;
      color: $blue-400;
    }
    @include screen(laptop) {
      &:hover {
        color: $blue-400;
      }
    }
    &.active {
      color: $blue-400;
      &:before {
        background-color: $blue-400;
      }
    }
  }
  &__container {
    display: none;
    overflow-x: auto;
    img {
      min-width: 1000px;
      @include screen(laptop) {
        width: 100%;
      }
    }
    &.active {
      display: block;
    }
  }
}