.header {
  border-bottom: 1px solid $brown-100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  transition: background-color $transition-default;
  &.active {
    background-color: $white;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__logo {
    margin-right: 104px;
  }
  &__nav {
    display: none;
    align-items: center;
    gap: 32px;
    &.active {
      display: flex;
    }
    @include screen(laptop) {
      display: flex;
    }
  }
  &__link {
    color: $gray-500;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    @include screen(laptop) {
      transition: color $transition-default;
      &:hover {
        color: $blue-400;
      }
    }
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__lang {
    &__text {
      display: flex;
      align-items: center;
    }
    &__flag {
      max-width: 22px;
      width: 100%;
    }
    &__current {
      border-radius: 6px;
      border: 1px solid rgba(214, 219, 226, 0.4);
      padding: 8px 8px 8px 16px;
      display: flex;
      align-items: center;
      gap: 16px;
      &:hover {
        span {
          color: $blue-400;
        }
      }
      span {
        color: $gray-400;
        font-size: 12px;
        font-weight: 600;
        line-height: 16px;
        letter-spacing: 1.44px;
        text-transform: uppercase;
        transition: color $transition-default;
      }
    }
  }
  &__button {
    display: flex;
    align-items: center;
    gap: 16px;
    border-radius: 6px;
    background-color: $blue-400;
    padding: 8px;
    @include screen(laptop) {
      padding: 8px 16px;
      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-500;
      }
    }
    span {
      text-transform: uppercase;
      font-size: 12px;
      font-weight: 600;
      line-height: 24px;
      color: $white;
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
  }
}