.install {
  padding: 45px 0 100px;
  @include screen(tablet) {
    padding: 45px 0 130px;
  }
  @include screen(laptop) {
    padding: 45px 0 230px;
  }
  &__items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 24px 0 24px 24px;
    gap: 64px;
    counter-reset: numbers;
    @include screen(tablet) {
      padding: 40px 80px;
    }
    @include screen(laptop) {
      gap: 88px;
      padding: 56px 112px 0;
    }
  }
  &__item {
    position: relative;
    &:before {
      counter-increment: numbers;
      content: counter(numbers);
      position: absolute;
      font-weight: 900;
      left: -40px;
      top: -50px;
      font-size: 100px;
      line-height: 120px;
      opacity: 0.5;
      background: linear-gradient(
        180deg,
        #aad302 0%,
        rgba(141, 173, 10, 0) 83.67%
      );
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      @include screen(laptop) {
        font-size: 279px;
        line-height: 300px;
        left: -100px;
        top: -100px;
      }
    }
  }
  &__fluid {
    background-color: $white;
    box-shadow: 0px 4px 32px rgba(2, 18, 87, 0.1);
    min-height: 232px;
    display: flex;
    gap: 40px;
    position: relative;
    z-index: 2;
    width: 100%;
    justify-content: space-between;
    flex-direction: column;
    padding: 24px;
    align-items: flex-start;
    @include screen(tablet) {
      padding: 32px;
    }
    @include screen(laptop) {
      align-items: stretch;
      flex-direction: row;
      padding: 40px 64px;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__description {
    font-size: 18px;
    color: $gray-400;
    line-height: 24px;
  }
  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 40px;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    border-radius: 6px;
    background-color: $blue-400;
    min-width: 230px;
    padding: 8px;
    @include screen(laptop) {
      align-self: flex-end;
      padding: 12px 16px;
      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-500;
      }
    }
    @include screen(desktop) {
      min-width: 272px;
    }
    span {
      text-transform: uppercase;
      font-size: 12px;
      font-weight: 600;
      line-height: 24px;
      color: $white;
    }
  }
}